import React , { useEffect } from "react";
import axios from "axios";

var UserStateContext = React.createContext();
var UserDispatchContext = React.createContext();

function userReducer(state, action) {
  switch (action.type) {
    case "LOGIN_SUCCESS":
      return { ...state, isAuthenticated: true, error: null, ...action.payload};
    case "SIGN_OUT_SUCCESS":
      return { ...state, isAuthenticated: false , name:null, email:null, error: null};
    case "LOGIN_FAILURE":
      return {...state, isAuthenticated: false , name:null, email:null, error:action.payload.error };
    case "REGISTER_RESULT":
      return {...state, isAuthenticated: false , name:null, email:null, error:action.payload.msg };
    default: {
      throw new Error(`Unhandled action type: ${action.type}`);
    }
  }
}

function UserProvider({ children }) {
  var [state, dispatch] = React.useReducer(userReducer, {
    isAuthenticated: false,
    name: null,
    email: null,
    error: null,
    role: null
  });

  useEffect(()=>{
    axios.get("/users/userinfo")
      .then(res => {
        if(res.data && res.data.email){
          dispatch({ type: 'LOGIN_SUCCESS', payload: res.data })
        }
        else{
          dispatch({ type: 'LOGIN_FAILURE', payload:{error: null}})
        }
      })
  },[])

  return (
    <UserStateContext.Provider value={state}>
      <UserDispatchContext.Provider value={dispatch}>
        {children}
      </UserDispatchContext.Provider>
    </UserStateContext.Provider>
  );
}

function useUserState() {
  var context = React.useContext(UserStateContext);
  if (context === undefined) {
    throw new Error("useUserState must be used within a UserProvider");
  }
  return context;
}

function useUserDispatch() {
  var context = React.useContext(UserDispatchContext);
  if (context === undefined) {
    throw new Error("useUserDispatch must be used within a UserProvider");
  }
  return context;
}

export { UserProvider, useUserState, useUserDispatch, loginUser, registerUser, signOut, loginUserWithFB };

// ###########################################################
//como setIsLoading Y setError mando referencias a los estados en el componente que llama esta funcion
function loginUser(dispatch, login, password, history, setIsLoading) {
  setIsLoading(true);

  //double exclamation marks cast the string value into boolean, so if login or password are "" empty arrays it will gives false
  if (!!login && !!password) {
    axios.post("/users/login?type=sendResults", {email: login , password: password})
      .then(res =>{
        dispatch({ type: 'LOGIN_SUCCESS', payload: res.data })
        setIsLoading(false)
        history.push('/app/dashboard')
      })
      .catch(err =>{
        setIsLoading(false);
        dispatch({ type: 'LOGIN_FAILURE' , payload:{error: err.response.data}})
      })
  } else {
    dispatch({ type: "LOGIN_FAILURE" , payload:{error: "Missing email or password"}});
    setIsLoading(false);
  }
}

function registerUser(dispatch, email, name, password , password2 , history, setIsLoading) {
  setIsLoading(true);

  //double exclamation marks cast the string value into boolean, so if login or password are "" empty arrays it will gives false
  if (!!email && !!password && !!password2 && !!name && password === password2) {
    axios.post("/users/register?type=sendResults", {name ,email , password, password2})
      .then(res =>{
        dispatch({ type: 'REGISTER_RESULT', payload: {msg:res.data} })
        setIsLoading(false)
      })
      .catch(err =>{
        setIsLoading(false);
        dispatch({ type: 'REGISTER_RESULT' , payload:{msg: err.response.data}})
      })
  } else {
    dispatch({ type: "REGISTER_RESULT" , payload:{msg: "Complete the registration form!"}});
    setIsLoading(false);
  }
}

function loginUserWithFB(dispatch, history, setIsLoading) {
  setIsLoading(true);
  window.open('/users/loginFacebook?redirectTo=/%23/app/dashboard&redirectOnFail=/', "_self");
}

function signOut(dispatch, history) {
  axios.get("/users/logout?type=sendResults")
    .then(res =>{
      history.push("/login");
      dispatch({ type: "SIGN_OUT_SUCCESS" });
    })
}
