import React from "react";
import {
  Grid,
} from "@material-ui/core";

// styles
import useStyles from "./styles";

// components
import mock from "./mock";
// context
import { useDataState } from "../../context/DataContext";
import PageTitle from "../../components/PageTitle";
import BigStat from "./components/BigStat/BigStat";
import BigChart from "../../components/PreEnsambled/BigChart";

const mainChartData = getMainChartData(12);

export default function Inflation(props) {
  var classes = useStyles();
  const monthsLoaded = useDataState().monthsLoaded;
  const yearInflationLoaded = useDataState().anualInflation;
  const chartInflationLoaded = monthsLoaded? Object.keys(monthsLoaded).map(month =>{
    return {"Variacin": monthsLoaded[month].inflacionTotal.toFixed(2)}
  }).reverse(): mainChartData;

  const monthsCount = monthsLoaded.length;
  const firstMonthDate =  monthsCount > 0? monthsLoaded[monthsCount-1].fecha : 0;
  const firstMonth = firstMonthDate !== 0? firstMonthDate.split("-")[0] : 0;

  let categoriesLoaded = mock.bigStat;
  if(monthsCount>0){
    categoriesLoaded = monthsLoaded[0].inflacionPorCategoria.map(category =>{
      return {
        categoria: category.nombre,
        sensados: {
          anual: 0,
          mensual: category.cantidadProductosComparados
        },
        color: "primary",
        inflacion: {
          anual: { value: 0, profit: true },
          mensual: { value: category.inflacionPromedio.toFixed(2), profit: category.inflacionPromedio > 0? true:false },
          monthlyInflation: []
        }
      }
    })

    categoriesLoaded.forEach(category =>{
      //add all the last 12 inflations
      let monthsCounted = 12;
      monthsLoaded.map(month =>{
        monthsCounted--;
        if(month.inflacionPorCategoria){
          //only add extra data to the chart of the category up to 12 months otherwise the chart is dificult to distinguish
          if(monthsCount>= 0){
            const subaMes = month.inflacionPorCategoria.filter(prevCateg => prevCateg.nombre === category.categoria)[0].inflacionPromedio;
            const sensadosMes = month.inflacionPorCategoria.filter(prevCateg => prevCateg.nombre === category.categoria)[0].cantidadProductosComparados;
            category.inflacion.anual.value += ((category.inflacion.anual.value * subaMes)/100) + subaMes;
            category.sensados.anual += sensadosMes;
            category.inflacion.monthlyInflation.push({ value:subaMes})
          }
        }
      })
      category.inflacion.anual.value = category.inflacion.anual.value.toFixed(2);
    })
  }

  /*const categoriesLoaded = monthsLoaded? Object.keys(monthsLoaded).map(month =>{
       monthsLoaded[month].inflacionPorCategoria.map(category => {

       })
  }).reverse(): mock.bigStat;*/




  return (
    <>
      <PageTitle title="Variacin De Precios En Supermercados" button="Ultimo reporte" />
      <Grid container spacing={4}>
        <BigChart title="Inflacion Mensual" firstMonth={firstMonth} data={chartInflationLoaded} categories={["Variacin"]} />
        {categoriesLoaded.map(stat => (
          <Grid item md={4} sm={6} xs={12} key={stat.categoria}>
            <BigStat {...stat} />
          </Grid>
        ))}
      </Grid>
    </>
  );
}

// #######################################################################
function getRandomData(length, min, max, multiplier = 1, maxDiff = 2) {
  var array = new Array(length).fill();
  let lastValue;

  return array.map((item, index) => {
    let randomValue = Math.random() *multiplier;

    while (
      randomValue <= min ||
      randomValue >= max ||
      (lastValue && randomValue - lastValue > maxDiff)
    ) {
      randomValue = Math.random()*multiplier;
    }

    lastValue = randomValue;

    return { value: randomValue };
  });
}

function getMainChartData(lenght = 4) {
  var resultArray = [];
  var inflacionMensual = getRandomData(lenght, 0, 2, 5, 5);

  for (let i = 0; i < inflacionMensual.length; i++) {
    resultArray.push({
      Inflacion: parseFloat(inflacionMensual[i].value.toFixed(2))
    });
  }

  return resultArray;
}
