import React from "react";
import {
  Route,
  Switch,
  Redirect,
  withRouter,
} from "react-router-dom";
import classnames from "classnames";

// styles
import useStyles from "./styles";

// components
import Header from "../Header";
import Sidebar from "../Sidebar";

// pages
import Dashboard from "../../pages/dashboard";
import Notifications from "../../pages/notifications";
import Maps from "../../pages/maps";
import Tables from "../../pages/tables";
import Supermercados from "../../pages/supermercados";
import Marcas from "../../pages/marcas";
import Icons from "../../pages/icons";
import Charts from "../../pages/charts";
import Apideprecios from "../../pages/apideprecios";
import Inflation from "../../pages/Inflation";

// context
import { useLayoutState } from "../../context/LayoutContext";

function Layout(props) {
  var classes = useStyles();

  // global
  var layoutState = useLayoutState();
  let routes = <Switch>
                    <Route path="/app/Dashboard" component={Inflation} />
                    <Route path="/app/Inflation" component={Inflation} />
                    <Route path="/app/supermercados" component={Supermercados} />
                    <Route path="/app/marcas" component={Marcas} />
                    <Route path="/app/maps" component={Maps} />
                </Switch> ;

  if(props.isAuthenticated && props.role === "admin"){
    routes = <Switch>
      <Route path="/app/Dashboard" component={Inflation} />
      <Route path="/app/Inflation" component={Inflation} />
      <Route path="/app/supermercados" component={Supermercados} />
      <Route path="/app/marcas" component={Marcas} />
      <Route path="/app/maps" component={Maps} />
      <Route path="/app/notifications" component={Notifications} />
      <Route path="/app/apideprecios" component={Apideprecios} />
      <Route
        exact
        path="/app/ui"
        render={() => <Redirect to="/app/ui/icons" />}
      />
      <Route path="/app/ui/icons" component={Icons} />
      <Route path="/app/ui/charts" component={Charts} />
    </Switch> ;
  }

  return (
    <div className={classes.root}>
        <>
          <Header history={props.history} name={props.name} role={props.role} />
          <Sidebar role={props.role}/>
          <div
            className={classnames(classes.content, {
              [classes.contentShift]: layoutState.isSidebarOpened,
            })}
          >
            <div className={classes.fakeToolbar} />
            {routes}
          </div>
        </>
    </div>
  );
}

export default withRouter(Layout);
