import React , { useEffect } from "react";
import axios from "axios";

var DataStateContext = React.createContext();
var DataDispatchContext = React.createContext();

function dataReducer(state, action) {
  switch (action.type) {
    case "LOAD_DATA":
      return { ...state, monthsLoaded: action.payload, anualInflation: action.yearInflation};
    case "FAIL_LOADING_DATA":
      return { ...state, ...action.payload};
    default: {
      throw new Error(`Unhandled action type: ${action.type}`);
    }
  }
}

function DataProvider({ children }) {
  var [state, dispatch] = React.useReducer(dataReducer, {
    monthsLoaded: [],
    anualInflation: [],
    anualCategoryInflation:[],
    error: null
  });

  useEffect(()=>{
    var lastMonthLoaded = localStorage['lastMonthLoaded'];
    var montlyDataString = localStorage['monthlyInflationData'];
    var monthlyInflationData = montlyDataString? JSON.parse(montlyDataString): montlyDataString;
    if(monthlyInflationData !== undefined){
      const anualInflation = getAnualInflation(monthlyInflationData);
      dispatch({ type: 'LOAD_DATA', payload: monthlyInflationData , yearInflation: anualInflation});
    }

    const date = new Date;
    if(lastMonthLoaded !== (("0" + (date.getMonth())).slice(-2))+"-"+date.getFullYear()){
      //server check for missing months in the local machine and pass them here
      axios.get("/apis/getInflation/"+lastMonthLoaded)
        .then(res => {
          if(res.data){
            if(res.data[0].fecha){
              let arrayResult = Object.keys(res.data).map(month => res.data[month]);
              if(monthlyInflationData !== undefined){
                arrayResult.push(...monthlyInflationData);
              }
              localStorage.setItem('monthlyInflationData', JSON.stringify(arrayResult));
              const lastMonthUpdated = arrayResult[0].fecha;
              localStorage['lastMonthLoaded'] = lastMonthUpdated;

              const anualInflation = getAnualInflation(arrayResult);
              dispatch({ type: 'LOAD_DATA', payload: arrayResult , yearInflation: anualInflation})
            }
            else{
              dispatch({ type: 'FAIL_LOADING_DATA', payload:{error: res.data}})
            }
          }
          else{
            dispatch({ type: 'FAIL_LOADING_DATA', payload:{error: "No data returned"}})
          }
        })
        .catch(err =>{
          dispatch({ type: 'FAIL_LOADING_DATA', payload:{error: err}})
        })
    }
  },[])

  return (
    <DataStateContext.Provider value={state}>
      <DataDispatchContext.Provider value={dispatch}>
        {children}
      </DataDispatchContext.Provider>
    </DataStateContext.Provider>
  );
}

function getAnualInflation(arrayResults){
  let years = [];
  arrayResults.forEach(monthObj =>{
    const [month, year] = monthObj.fecha.split("-");
    let yearsInstance = years.filter(x => x.yearNumber === year);
    if(yearsInstance.length<=0){
      years.push({yearNumber: year, inflation: monthObj.inflacionTotal})
    }
    else{
      yearsInstance[0].inflation += ((yearsInstance[0].inflation * monthObj.inflacionTotal)/100)+monthObj.inflacionTotal;
    }
  })
  return years;
}

function useDataState() {
  var context = React.useContext(DataStateContext);
  if (context === undefined) {
    throw new Error("useUserState must be used within a UserProvider");
  }
  return context;
}

function useDataDispatch() {
  var context = React.useContext(DataDispatchContext);
  if (context === undefined) {
    throw new Error("useUserDispatch must be used within a UserProvider");
  }
  return context;
}

export { DataProvider, useDataState, useDataDispatch };
