import React from "react";
import { Grid } from "@material-ui/core";
import MUIDataTable from "mui-datatables";

// components
import PageTitle from "../../components/PageTitle";
import mock from "./mock";
import { useDataState } from "../../context/DataContext";

const getSupermercadoTable = (monthsLoaded)=>{
  return {
    title:"Ranking de Supermercados segun variacin de precios",
    columns: ["Razon Social", "Supermercados", "Productos Censados", "Variacin De Precios"],
    data: monthsLoaded[0].inflacionPorComercio.map(comercio =>{
      const banderas = comercio.InflacionPorBandera.map(bandera => bandera.banderaDescripcion+": "+bandera.inflacionPromedio.toFixed(2)+"%")
      return [comercio.comercioRazonSocial, banderas.join( " - ") , comercio.cantidadProductosComparados, comercio.inflacionPromedio.toFixed(2)+"%"]
    })
  }
}

export default function Supermercados(props) {
  const monthsLoaded = useDataState().monthsLoaded;
  let tableTitle = "Mock data test";
  let dataLoaded = mock.dataTable;
  let columns = ["test 1", "test 2", "test3 ", "test 4"];
  if(monthsLoaded.length > 0){
    let supermarketLoaded = getSupermercadoTable(monthsLoaded);
    tableTitle = supermarketLoaded.title;
    dataLoaded = supermarketLoaded.data;
    columns = supermarketLoaded.columns;
  }


  return (
    <>
      <PageTitle title="Supermercados" />
      <Grid container spacing={4}>
        <Grid item xs={12}>
          <MUIDataTable
            title={tableTitle}
            data={dataLoaded}
            columns={columns}
            options={{
              filterType: "checkbox",
            }}
          />
        </Grid>
      </Grid>
    </>
  );
}
