import React, { useState } from "react";
import PropTypes from "prop-types";
import { Typography } from "../Wrappers";
import Dot from "../Sidebar/components/Dot";
import { Grid, MenuItem, OutlinedInput, Select } from "@material-ui/core";
import Widget from "../Widget";
import { useTheme } from "@material-ui/styles";
import useStyles from "./styles";
import { Tooltip, Line, LineChart, ResponsiveContainer, XAxis, YAxis, CartesianGrid } from "recharts";

const BigChart =(props) =>{
  var classes = useStyles();
  var theme = useTheme();
  var months = [
    'Enero', 'Febrero', 'Marzo', 'Abril', 'Mayo',
    'Junio', 'Julio', 'Agosto', 'Septiembre',
    'Octubre', 'Noviembre', 'Diciembre'
  ];

  if(props.firstMonth){
    let modifyMonths = months.splice(props.firstMonth);
    months.forEach(month => modifyMonths.push(month));
    months = modifyMonths;
  }

  //let yAxisMaxValue = Math.max.apply(Math, props.data.map(function(object) { return object[props.categories[0]]; }))

  // local
  var [mainChartState, setMainChartState] = useState("Mensual");
  return(
    <Grid item xs={12}>
      <Widget
        bodyClass={classes.mainChartBody}
        header={
          <div className={classes.mainChartHeader}>
            <Typography
              variant="h5"
              color="text"
              colorBrightness="secondary"
            >
            </Typography>
            <div className={classes.mainChartHeaderLabels}>
              {props.categories.map((category =>
                  <div key={category} className={classes.mainChartHeaderLabel}>
                    <Dot color="warning" />
                    <Typography className={classes.mainChartLegentElement}>
                      {category}
                    </Typography>
                  </div>
              ))}
            </div>
            <Select
              value={mainChartState}
              onChange={e => setMainChartState(e.target.value)}
              input={
                <OutlinedInput
                  labelWidth={0}
                  classes={{
                    notchedOutline: classes.mainChartSelectRoot,
                    input: classes.mainChartSelect,
                  }}
                />
              }
              autoWidth
            >
              <MenuItem value="Mensual">Mensual</MenuItem>
              <MenuItem value="Anual">Anual</MenuItem>
            </Select>
          </div>
        }
      >
        <ResponsiveContainer width="100%" minWidth={500} height={350}>
          <LineChart
            margin={{ top: 40, right: 30, bottom: 5, left: 0 }}
            data={props.data}
          >
            <Tooltip isAnimationActive={false} allowEscapeViewBox={{ x: false, y: false }}/>
            <CartesianGrid stroke="#eee" strokeDasharray="5 5"/>
            <YAxis
              tickFormatter={i => i+"%"}
              tick={{ fill: theme.palette.text.hint + "80", fontSize: 14 }}
              stroke={theme.palette.text.hint + "80"}
              tickLine={false}
            />
            <XAxis
              tickFormatter={ (i)=> months[i++]}
              tick={{ fill: theme.palette.text.hint + "80", fontSize: 14 }}
              stroke={theme.palette.text.hint + "80"}
              tickLine={false}
            />
            {props.categories.map((category =>
                <Line
                  key={category}
                  label ={{ fill: 'blue', fontSize: 20 , dy:-15}}
                  type="linear"
                  dataKey={category}
                  stroke={theme.palette.warning.main}
                  strokeWidth={2}
                  dot={{
                    stroke: theme.palette.warning.dark,
                    strokeWidth: 2,
                    fill: theme.palette.warning.main,
                  }}
                />
            ))}
          </LineChart>
        </ResponsiveContainer>
      </Widget>
    </Grid>

  )
}
BigChart.propTypes = {
  title: PropTypes.string.isRequired,
  categories: PropTypes.array.isRequired,
  data: PropTypes.array.isRequired
};

export default BigChart;