import React from "react";
import { Grid } from "@material-ui/core";
import MUIDataTable from "mui-datatables";

// components
import PageTitle from "../../components/PageTitle";
import mock from "./mock";
import { useDataState } from "../../context/DataContext";
const getMarcasTable = (monthsLoaded)=>{
  return {
    title:"Ranking de Marcas segun aumento de precios",
    columns: ["Marca", "Productos Censados", "Variacin De Precios"],
    data: monthsLoaded[0].inflacionPorMarca.map(marcaObj =>{
      return [marcaObj.marca, marcaObj.cantidadProductosComparados , marcaObj.inflacionPromedio.toFixed(2) +"%"]
    })
  }
}


export default function Marcas(props) {
  const monthsLoaded = useDataState().monthsLoaded;
  let tableTitle = "Mock data test";
  let dataLoaded = mock.dataTable;
  let columns = ["test 1", "test 2", "test3 ", "test 4"];
  if(monthsLoaded.length > 0){
    let marcasLoaded = getMarcasTable(monthsLoaded);
    tableTitle = marcasLoaded.title;
    dataLoaded = marcasLoaded.data;
    columns = marcasLoaded.columns;
  }


  return (
    <>
      <PageTitle title="Marcas" />
      <Grid container spacing={4}>
        <Grid item xs={12}>
          <MUIDataTable
            title={tableTitle}
            data={dataLoaded}
            columns={columns}
            options={{
              filterType: "checkbox",
            }}
          />
        </Grid>
      </Grid>
    </>
  );
}
